@import "https://fonts.googleapis.com/css2?family=Cardo:ital,wght@0,400;0,700;1,400&display=swap";
body {
  color: var(--first-background-color);
  letter-spacing: .02em;
  background-color: #eeeee9;
  margin: 0;
  font-family: Cardo, serif;
  font-size: 16px;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

button {
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.site-item {
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  display: flex;
}

.site-link {
  font-family: inherit;
  font-size: 26px;
}

.site-link:hover {
  color: #f8d24e;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

.header {
  width: 100%;
  z-index: 1000;
  background-color: #282828;
  padding: 30px;
  position: fixed;
}

[class$="-container"] {
  max-width: 985px;
  margin: 0 auto;
}

.nav-list {
  justify-content: center;
  gap: 30px;
  display: flex;
}

.nav-link {
  color: #fcfcfc;
  padding: 11px 18px;
  font-family: inherit;
  font-size: 18px;
  transition: color .5s cubic-bezier(.4, 0, .2, 1);
  position: relative;
}

.nav-link:after {
  content: "";
  height: 2px;
  width: 100%;
  background-color: #caff34;
  transition: all .4s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  bottom: 0;
  left: 0;
  transform: scaleX(0);
}

.nav-link:hover {
  color: #caff34;
}

.nav-link:hover:after {
  transform: scaleX(1);
}

.hero {
  padding-top: 149px;
}

.hero-container {
  justify-content: center;
  align-items: center;
  display: flex;
}

.hero-title {
  color: #282828;
  font-family: inherit;
  font-size: 80px;
  font-style: italic;
  font-weight: 700;
}

.hero-text {
  color: #282828;
  max-width: 421px;
  background-color: #caff34;
  margin-top: 42px;
  font-family: inherit;
  font-size: 24px;
  font-weight: 700;
}

.hero-img {
  width: 537px;
  height: 669px;
}

footer {
  background-color: #282828;
}

.footer-wrapper {
  justify-content: space-between;
  align-items: center;
  margin-top: 94px;
  display: flex;
}

.footer-contacts {
  color: #eeeee9;
  justify-content: center;
  gap: 5px;
  font-family: inherit;
  font-size: 18px;
  transition: color .5s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.footer-contacts:hover {
  color: #caff34;
}

.contact-icon {
  width: 20px;
  height: 20px;
  fill: #eeeee9;
}

.footer-title {
  color: #eeeee9;
  font-family: inherit;
  font-size: 32px;
  font-weight: 700;
  -webkit-text-decoration: underline #caff34;
  text-decoration: underline #caff34;
}

.icons-list {
  justify-content: center;
  gap: 15px;
  margin-top: 36px;
  display: flex;
}

.icons-item {
  width: 32px;
  height: 32px;
}

.icons-link {
  width: 100%;
  height: 100%;
  background-color: #fcfcfc;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: background-color .5s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.icons-link:hover {
  background-color: #caff34;
}

.footer-icon {
  width: 20px;
  height: 20px;
  fill: #282828;
}

.footer-ending-text {
  text-align: center;
  color: #6e6e6e;
  margin-top: 100px;
  font-size: 12px;
}

